<template>
  <div id="app-main"
       class="layout white">
    <div class="header layout-header-bar animated fadeInDown">
      <div class="head">
        <div class="logo">
          <!-- <span class="iconfont icon-a-lianhe64"></span> -->
          <!-- <span class="img"
                @click="isCollapsed=!isCollapsed">
            <img :src="imgLogo"
                 alt=""
                 style="width: 120px;height: 33.59px; object-fit: cover;">
          </span> -->
          <img src="./../../../static/img/logo_name.png"
                 style="width: 120px;height: 33.59px; object-fit: cover;">
          <div class="line"></div>
          <span class="tit">发动机数字孪生健康管理</span>
        </div>
        <ul class="menu-plist"
            ref="menu">
          <li class="menu-item-content"
              v-for="(menu,index) in menuList"
              :key="menu.title"
              @click="toggleSubMenu(menu)"
              :class="{
             selected:currentSelectMenu==menu && isSubmenuVisible,
             active:isMenuActive(menu)}"
              v-show=" index<tip ? true:false">
            <span :class="['iconfont', menu.icon]"></span>
            <span v-if="menu.subMenus"
                  class="span">{{menu.title}}</span>
            <!--            <span v-else-if="menu.path" @click="$router.push({path:`${menu.path}${menu.hash || ''}`})">-->
            <!--              {{menu.title}}-->
            <!--            </span>-->
            <router-link v-else-if="menu.route"
                         :to="menu.subMenus ?{}:{name:menu.route, params: menu.params,query:menu.query}">
              <span class="span">{{menu.title}}</span>
            </router-link>
          </li>
          <Dropdown :transfer="true"
                    trigger="click"
                    v-show="showTip">
            <li href="javascript:void(0)"
                style="cursor: pointer">
              ...
            </li>
            <DropdownMenu slot="list">
              <DropdownItem v-for="(menu,index) in menuList"
                            class="menu-sub"
                            :key="menu.name"
                            v-if=" index >= tip ? true : false ">
                <li class="menu-item-content"
                    :key="menu.title"
                    @click="toggleSubMenu(menu)"
                    :class="{
             selected:currentSelectMenu==menu && isSubmenuVisible,
             active:isMenuActive(menu)}"
                    v-if="index>=tip ? true:false">
                  <span v-if="menu.subMenus"
                        class="span">{{menu.title}}</span>
                  <router-link v-else-if="menu.route"
                               :to="menu.subMenus ?{}:{name:menu.route, params: menu.params,query:menu.query}">
                    <span class="span">{{menu.title}}</span>
                  </router-link>
                </li>
              </DropdownItem>
            </DropdownMenu>
          </Dropdown>
          <!--          <ul class="sub-ul">-->
          <!--            <li class="menu-item-content" v-for="(menu,index) in menuList" :key="menu.title" @click="toggleSubMenu(menu)"-->
          <!--                :class="{-->
          <!--             selected:currentSelectMenu==menu && isSubmenuVisible,-->
          <!--             active:isMenuActive(menu)}"-->
          <!--                :style="{display:index>tip ? 'none':'block'}"-->
          <!--            >-->
          <!--              <span v-if="menu.subMenus" class="span">{{menu.title}}</span>-->
          <!--              <router-link v-else-if="menu.route"-->
          <!--                           :to="menu.subMenus ?{}:{name:menu.route, params: menu.params,query:menu.query}">-->
          <!--                <span class="span">{{menu.title}}</span>-->
          <!--              </router-link>-->
          <!--            </li>-->
          <!--          </ul>-->

        </ul>
        <!-- <MessageTip class="message-tip"
                    @on-message-change="onMessageChange">
          <Badge dot
                 :count="tipCount"
                 type="error"
                 class="badgg">
            <Icon type="ios-notifications-outline"
                  size="26"
                  class="demo-badge"></Icon>
          </Badge>
        </MessageTip> -->
        <User class="header-user"></User>

      </div>

    </div>
    <div class="main-content">
      <div :class="['indent', isCollapsed? 'collapsed': '']">
        <span class="iconfont icon-a-shousuo1"
              @click="toggle"></span>
      </div>
      <div class="main-sider animated slideInLeft"
           :class="{collapsed:isCollapsed,'has-submenu':subMenuVisible}">
        <!-- <p class="t-title"> -->
        <!--          <Tooltip :content="currentSelectMenu.title" placement="right" v-if="isCollapsed" theme="light">-->
        <!--            <Icon type="ios-film" />-->
        <!-- </Tooltip> -->
        <!-- <span>{{currentSelectMenu.title}}</span> -->
        <!-- </p> -->
        <li class="menu-item"
            v-for="(smenu,sindex) in currentSelectMenu.subMenus"
            :class="{active:isMenuActive(smenu)}"
            :key="smenu.title">
          <router-link v-if="smenu.route"
                       :to="{ path: smenu.path,name:smenu.route, params: smenu.params,query:smenu.query}">
            <div class="menu-item-content"
              :style="{paddingLeft:isCollapsed?'0':'20px',
                       display:isCollapsed?'flex':'block',
              }"
            >
              <Tooltip :content="smenu.title"
                        placement="right"
                       v-if="isCollapsed"
                       theme="dark">
                <i :class="['iconfont', smenu.icon]"></i>
              </Tooltip>
              <i v-if="!isCollapsed" :class="['iconfont', smenu.icon]"></i>
              <span v-if="!isCollapsed">{{smenu.title}}</span>
            </div>
          </router-link>
        </li>
      </div>

      <div class="layout-content animated fadeInUp"
           :class="{collapsed:isCollapsed,'has-submenu':false}"
           @click="hideSubmenu">
           <!-- nocollapsed:!currentSelectMenu.subMenus -->
        <div class="layout-content-inner">
          <transition :enter-active-class="routerTransitionClass.enter"
                      :leave-active-class="routerTransitionClass.leave">
            <router-view class="child-view" />
          </transition>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import './main.less'
import User from './components/user'
import MessageTip from '@/components/message-tip'
import RouterTransition from '@/mixins/router-transition'

const iconMessage = require('../../../static/img/icon-message.png')
export default {
  name: 'app-main',
  data () {
    return {
      isCollapsed: false,
      subMenuVisible: false,
      tipCount: 0,
      showTip: false,
      tip: 100,
      iconMessage,
      currentSelectMenu: {},
      isSubmenuVisible: false,
      extraMenus: [
        {
          icon: './static/img/icon-menu-device.png',
          orderIndex: 999,
          route: 'control-device-list',
          title: '设备控制'
        },
        {
          icon: './static/img/icon-menu-device.png',
          orderIndex: 1000,
          title: '报告管理',
          subMenus: [
            {
              orderIndex: 1001,
              route: 'report-list',
              title: '模板维护'
            },
            {
              orderIndex: 1002,
              route: 'report-generate',
              title: '报告生成'
            }
          ]
        },
        {
          icon: './static/img/icon-menu-device.png',
          orderIndex: 111,
          title: '健康诊断',
          subMenus: [
            {
              orderIndex: 222,
              route: 'health-manage',
              title: '健康管理'
            }
          ]
        }
      ],
      menus: [
        {
          icon: 'icon-shebei1',
          orderIndex: 1,
          title: '设备管理',
          subMenus: [
            {
              icon: 'icon-qingdan',
              orderIndex: 6,
              route: 'device-runInfo',
              title: '运行概况'
            },
            {
              icon: 'icon-shebeileixing',
              orderIndex: 31,
              route: 'device-fault-deal',
              title: '故障诊断'
            },
            {
              icon: 'icon-shebeixinghao',
              orderIndex: 32,
              route: 'device-life-predict',
              title: '寿命预测'
            },
            {
              icon: 'icon-guzhangdaima',
              orderIndex: 33,
              route: 'device-operation-decision',
              title: '运维决策'
            }

          ]
        }
      ]
    }
  },
  mixins: [RouterTransition],
  computed: {
    menuList () {
      // console.log(this.$store.state.user.routes.menus)
      // console.log('---------------------------', this.$store.state.user.routes.menus)
      // return this.$store.state.user.routes.menus
      return this.menus
    },
    hasSubMenu () {
      return !!(this.currentSelectMenu && !!this.currentSelectMenu.subMenus)
    },
    routeName () {
      return this.$route.name
    }
    // imgLogo () {
    //   return (
    //     (this.$store.state.app.globalConfig &&
    //       this.$store.state.app.globalConfig.managerLogo) ||
    //     imgLogo
    //   )
    // }
  },
  watch: {
    isSubmenuVisible (v) {
      this.$emit('on-submenu-visible-change', v)
    },
    isCollapsed (v) {
      this.$emit('on-collapsed-change', v)
    },
    routeName (v) {
      if (this.isSubmenuVisible && this.menuList) {
        for (let i = 0; i < this.menuList.length; i++) {
          if (this.isMenuActive(this.menuList[i])) {
            this.currentSelectMenu = this.menuList[i]
          }
        }
      }
    },
    menuList () {
      this.getCurrent()
    }
  },
  mounted () {
    this.renderMenu()
    window.$(window).on('resize', () => {
      this.renderMenu()
    })
    this.$eventHub.$on('sl-main-hide-submenu', this.hideSubMenu)
    this.$emit('on-collapsed-change', this.isCollapsed)
    this.$emit('on-submenu-visible-change', this.hasSubMenu)
    this.getCurrent()
  },
  methods: {
    toggle () {
      this.isCollapsed = !this.isCollapsed
      // console.log(this.isCollapsed)
    },
    sum (arr) {
      return eval(arr.join('+'))
    },
    renderMenu () {
      let boxWidth = this.$refs.menu.clientWidth
      let allLis = this.$refs.menu.getElementsByClassName('menu-item-content')
      let widthList = [],
        totalWidth = 0,
        tips = [],
        hide = []
      Array.prototype.slice.call(allLis).forEach((li) => {
        // 获取每个li的高度，并追加到数组中
        widthList.push(li.clientWidth)
      })
      for (let i = 0; i < widthList.length; i++) {
        totalWidth += widthList[i]
        if (totalWidth > boxWidth) {
          tips.push(i)
        } else {
          hide.push(widthList[i])
        }
        let num = tips && tips.length > 0 && tips[0]
        this.tip = num || allLis.length
        this.showTip = totalWidth > boxWidth
        // }
      }
    },
    isMenuActive (menu) {
      if (menu.route === this.$route.name && !menu.path) {
        if (!menu.params && !menu.query) {
          return true
        } else {
          let isSame = true
          if (
            menu.params &&
            !(
              this.$route.params &&
              JSON.stringify(this.$route.params) === JSON.stringify(menu.params)
            )
          ) {
            isSame = false
          }

          if (
            menu.query &&
            !(
              this.$route.query &&
              JSON.stringify(this.$route.query) === JSON.stringify(menu.query)
            )
          ) {
            isSame = false
          }

          if (isSame) {
            return true
          }
        }
      }
      if (menu.route) {
        let route = this.$router.match(menu.route)
        if (
          route &&
          route.meta &&
          route.meta.subPages &&
          route.meta.subPages.indexOf(this.$route.name) !== -1
        ) {
          return true
        }
      }
      if (menu.subMenus) {
        for (let i = 0; i < menu.subMenus.length; i++) {
          if (this.isMenuActive(menu.subMenus[i])) {
            return true
          }
        }
      }
    },
    getMenuAuth (menu) {
      if (menu.auth) {
        return menu.auth
      }
      // 从路由里面取权限
      let routes = this.$router.options.routes
      let route = null
      for (let i = 0; i < routes.length; i++) {
        if (routes[i].name === menu.route) {
          route = routes[i]
        } else {
          route = findSubRoute(routes[i].children)
        }
        if (route) {
          break
        }
      }

      function findSubRoute (children) {
        if (!children) {
          return null
        }
        let route = null
        for (let i = 0; i < children.length; i++) {
          if (children[i].name === menu.route) {
            route = children[i]
          } else {
            route = findSubRoute(children[i].children)
          }
          if (route) {
            break
          }
        }
        return route
      }

      return (route && route.meta && route.meta.auth) || []
    },
    toggleSubMenu (menu) {
      if (menu) {
        /* if (this.currentSelectMenu === menu) {
            this.isSubmenuVisible = !this.isSubmenuVisible;
            return;
          } */
        this.currentSelectMenu = menu
        if (menu.subMenus && menu.subMenus.length > 0) {
          this.isSubmenuVisible = true
          this.$router.push({
            name: menu.subMenus[0].route
          })
        } else {
          this.isSubmenuVisible = false
        }
      } else if (this.currentSelectMenu) {
        this.isSubmenuVisible = !this.isSubmenuVisible
      } else {
        this.isSubmenuVisible = false
      }
    },
    hideSubMenu () {
      this.currentSelectMenu = {}
      this.isSubmenuVisible = false
    },
    onMessageChange (data) {
      this.tipCount = data.total
    },
    onMenuCollapseChange (v) {
      this.isCollapsed = v
    },
    onMenuSubChange (v) {
      this.subMenuVisible = v
    },
    hideSubmenu () {
      // this.$eventHub.$emit('sl-main-hide-submenu');
    },
    getCurrent () {
      if (this.menuList) {
        for (let i = 0; i < this.menuList.length; i++) {
          if (this.isMenuActive(this.menuList[i])) {
            this.currentSelectMenu = this.menuList[i]
          }
        }
      }
      // console.log(this.currentSelectMenu)
    }
  },
  components: {
    User,
    MessageTip
  },
  destroyed () {
    window.$(window).off('resize', () => {
      this.renderMenu()
    })
  }
}
</script>

<style lang="less" scoped>
@import '../../style/varient.less';
@import '../../style/color.less';

.menu-sub {
  a {
    color: #1f1f1f;
  }

  .menu-item-content {
    // border-bottom: 3px solid #fff;
    border-bottom: none;
    .span {
      color: #6e7685;
    }

    &:hover,
    &.selected,
    &.active {
      .span {
        color: @color-primary;
      }

      // border-bottom: 3px solid @color-primary;
       border-bottom: 3px solid #0084FF;
    }
  }
}

#app-main {
  height: 100%;
  min-height: 650px;
  font-size: @size-font-normal;
  background: #f0f3f8;
}

.layout {
  .header {
    height: 60px;
    line-height: 60px;
    z-index: 200;
    position: relative;
    // display: flex;
    .head {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      .logo {
        height: @size-nav-height;
        /*padding-left: 20px;*/
        margin-right: 181px;
        margin-left: 24px;
        display: flex;
        height: 40px;
        line-height: 40px;
        display: flex;
        align-items: center;
        .iconfont{
          margin-right: 20px;
          // color: #0084FF;
          color: #fff;
          font-size: 35px;
          vertical-align: middle;
        }

        .img {
          /*padding-left: 20px;*/
          width: 120px;
          margin-right: 20px;
          text-align: center;

          /*margin-right: 20px;*/
          vertical-align: middle;
          background: @color-primary;
          color: white;
          cursor: pointer;
          font-size: @size-font-llg;
        }
        .line{
          background: #fff;
          width: 1px;
          height: 20px;
          margin-left: 20px;
        }
        .tit {
          display: inline-block;
          font-size: @size-font-lg;
          font-family: Alibaba PuHuiTi;
          font-weight: bolder;
          color: #fff;
          letter-spacing: .2em;
          margin-left: 20px;
        }
      }

      .header-user {
        height: 60px;
      }

      .menu-plist {
        flex: 1;
        height: 100%;
        white-space: nowrap; //不换行
        overflow: hidden; //超出隐藏
        text-overflow: ellipsis; //变成...
        display: flex;
        width: 0;
        opacity: 0;
        .menu-item-content {
          list-style: none;
          height: 100%;
          line-height: 60px;
          padding: 0 15px;
          // cursor: pointer;

          &.active {
            .iconfont {
              color: @color-primary;
              vertical-align: middle;
            }
            .span {
              font-size: 14px;
            }
          }
        }
      }

      .message-tip {
        margin: 0 10px;
        /*height: 20px;*/
        /*line-height: 20px;*/
        /*margin-top: 18px;*/
        cursor: pointer;

        .badgg {
          width: 32px;
          height: 32px;

          border-radius: 4px;
          text-align: center;
          line-height: 32px;

          /deep/ .ivu-badge-dot {
            width: 4px;
            height: 4px;
            top: 2px;
            right: 1px;
          }

          /deep/ .ivu-icon {
            font-weight: bolder;
          }
        }
      }
    }
  }

  .main-content {
    height: calc(~'100vh -' @size-nav-height);
    position: relative;
    overflow: hidden;
    .indent {
      width: @size-width-sider;
      height: 48px;
      // background-color: rgba(240, 243, 248, 0.7);
      background-color: #262C3A;
      transition: all ease 0.4s;
      line-height: 48px;
      text-align: center;
      .iconfont {
        font-style: 13px;
        // color: #000000;
        color: #fff;
      }
      &.collapsed {
        width: @size-width-sider-collapse;
      }

      &.nocollapsed {
        width: 0;
      }
    }
    .main-sider {
      position: absolute;
      left: 0;
      top: 48px;
      width: @size-width-sider;
      bottom: 0;
      transition: all ease 0.4s;
      z-index: 30;
      color: @color-text-primary;
      &.collapsed {
        width: @size-width-sider-collapse;
      }

      &.nocollapsed {
        width: 0;
      }

      .t-title {
        height: 50px;
        line-height: 50px;
        padding-left: 20px;
        font-size: 16px;
        letter-spacing: 1.5px;
        overflow: hidden;
      }

      .menu-item {
        position: relative;
        list-style: none;
        height: 48px;
        line-height: 48px;
        font-size: 14px;
        a {
          .menu-item-content {
            padding-left: 20px;
            justify-content: center;
            .iconfont {
              margin-right: 10px;
              font-size: 14px;
            }
          }
        }

        &.active {
          a,
          .iconfont {
            color: @color-primary;
          }
          &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 2px;
            height: 100%;
            background: #0084ff;
          }
        }
      }
    }

    .layout-content {
      position: absolute;
      left: @size-width-sider;
      top: 0;
      right: 0;
      bottom: 0;
      transition: all ease 0.4s;
      z-index: 20;
      overflow: auto;

      &.has-submenu {
        left: @size-width-sider + @size-width-sider;
      }

      &.collapsed {
        left: @size-width-sider-collapse;

        &.has-submenu {
          left: @size-width-sider-collapse + @size-width-sider;
        }
      }

      &.nocollapsed {
        left: 0;
      }
    }
  }
}

.layout-content-inner {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  /*transition: all ease 0.4s;*/
  z-index: 20;
  overflow: auto;
}

.child-view {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 24px;
  /*transition: all ease 0.4s;*/
  z-index: 20;
  overflow: auto;
}
</style>



