<template>
  <div class="user-avator-dropdown user">
    <!-- <div class="user-container">
      <div style="margin-right:12px;">
        <Tooltip content="跳转算盘link" style="width:30px;display:flex;align-items:center">
          <img
            src="./../../../../../static/img/suanpanlink.jpg"
            style="width:80%;height:10%"
            @click="goToLink"
          />
        </Tooltip>
      </div>
      <div class="time">
        <Icon type="md-time" :size="$flexible(24)" />
        <span class="num">{{ time }}</span>
        <span>/</span>
      </div>
      <div class="info">
        <Dropdown>
          <a href="javascript:void(0)" class="username">
            <span class="name" style="margin-right:10px;">{{
              user.fullName || user.userName
            }}</span>
            <i class="iconfont icon-yonghu"></i>
          </a>
          <DropdownMenu slot="list">
            <DropdownItem>
              <div @click="logOut">退出登录</div>
            </DropdownItem>
          </DropdownMenu>
        </Dropdown>
      </div>
    </div> -->
    <!-- <Dropdown :transfer="true" trigger="click">
      <a href="javascript:void(0)" class="page">
        <span style="color: white">后</span>
        <i class="iconfont iconwode"></i>
      </a>
      <DropdownMenu slot="list">
        <DropdownItem>
          <div @click="logOut">退出登录</div>
        </DropdownItem>
        <DropdownItem>
          <div @click="toUserSetting">用户设置</div>
        </DropdownItem>
      </DropdownMenu>
    </Dropdown> -->
    <!--    <Button class="btn-logout" type="text" size="small" @click="logOut" :loading="logouting">{{$t('common.text.drop_out')}}</Button>-->
    <!--<Dropdown trigger="click" style="margin-left: 20px">
      <a href="javascript:void(0)">
        <Avatar :src="user.avator" :icon="'ios-contact'"/>
        <span class="user-name">{{user.fullName||user.userName}}</span>
      </a>
      <DropdownMenu slot="list">
        <DropdownItem @click.native="toggleScreenSize">{{$store.state.app.isBigScreen?"小屏模式":"大屏模式"}}</DropdownItem>
      </DropdownMenu>
    </Dropdown>
    <Button class="btn-logout" type="info" size="small" @click="logOut" :loading="logouting">退出</Button>
    -->
  </div>
</template>

<script>
import './user.less'
import { mapActions } from 'vuex'
import { Option, Select } from 'iview'
import { getLang, setLang } from '../../../../libs/util'
import { LANG_ENUM } from '../../../../libs/constants'
import moment from 'moment'
import { get3DLink } from '@/api/device'
const iconUser = require('../../../../../static/img/im-person.png')

export default {
  name: 'User',
  components: {
    Select,
    Option
  },
  data () {
    return {
      logouting: false,
      iconUser,
      lang: getLang(),
      langList: [
        {
          key: LANG_ENUM.EN_US,
          name: 'English'
        },
        {
          key: LANG_ENUM.ZH_CN,
          name: this.$t('common.text.china')
        }
      ],
      time: '',
      linkUrl: ''
    }
  },
  created () {
    this.currentTime()
  },
  watch: {
    lang (v) {
      this.$i18n.locale = v
      setLang(v)
      // window.location.reload()
    }
  },
  computed: {
    user () {
      return this.$store.state.user
    }
  },
  mounted () {
    get3DLink().then(res => {
      if (res.code === 200) {
        this.linkUrl = res.data
        console.log('suanpanLink接口获取成功')
      } else {
        console.log('suanpanLink接口获取失败')
      }
    })
    this.lang = this.$i18n.locale
  },
  methods: {
    ...mapActions(['handleLogOut']),
    goToLink () {
      window.open(this.linkUrl)
    },
    logOut () {
      this.logouting = true
      this.handleLogOut()
        .then(() => {})
        .finally(() => {
          this.logouting = false
          this.$router.replace({
            name: 'login'
          })
        })
    },

    toUserSetting () {
      if (this.$hasAccess('per.query')) {
        this.$router.push({
          name: 'user-setting'
        })
      } else {
        return false
      }
    },
    currentTime () {
      this.time = moment().format('HH:mm:ss')
      setTimeout(() => {
        this.currentTime()
      }, 1000)
    }
  }
}
</script>

<style lang="less" scoped>
@import "../../../../style/varient";
/deep/.ivu-tooltip-rel{
  display: flex !important;
}
.user-container {
  display: flex;
  align-items: center;
  .time {
    margin-right: 12px;
    .num {
      margin: 0 10px;
    }
  }
  .info {
    display: flex;
    align-items: center;
    .username {
      margin-right: 12px;
      color: #fff;
      .name {
        font-size: 16px;
      }
    }
    .iconfont {
      font-size: 24px;
    }
  }
}

.btn-logout {
  color: white;

  &:hover {
    color: @color-primary;
  }
}
.user {
  text-align: center;
  margin-right: 20px;
  // vertical-align: middle;
  .page {
    .iconfont {
      font-size: 18px;
      color: #6e7685;
    }
  }
}

.user-me {
  height: 100%;
  display: inline-block;
  cursor: pointer;
}
</style>



