import axios from '@/libs/api.request'


export const getNum = () => {
return axios.request({
url: 'order/getTaskNum',
method: 'get'
})
};

export const getMessageList = ({
                                    pageNumber,
                                    pageSize,
                                 isRead
                                  }) => {
  return axios.request({
    url: 'message/listNotice',
    params: {
      pageNumber,
      pageSize,
      isRead
    },
    method: 'get'
  })
};


export const setMessageReaded = ({
                                   noticeIds = []
                                  }) => {
  return axios.request({
    url: 'message/markRead',
    data: {
      noticeIds
    },
    method: 'put'
  })
};

export const deleteMessage = ({
                                noticeIds = []
                                  }) => {
  return axios.request({
    url: 'message/deleteNotice',
    data: {
      noticeIds
    },
    method: 'post'
  })
};


