<template>
<!--  trigger="hover"-->
  <Poptip
    class="message-tip"
    placement="bottom"
    :width="$flexible(300)"
    trigger="hover"
    @on-popper-show="refresh"
    :transfer="true"
  >
    <div class>
      <slot></slot>
    </div>
    <div class="message-tip-content" slot="content">
      <Tabs class="message-tip-tab" value="1">
        <TabPane
          :label="`${$t_p('remind')}(${tipPageInfo.totalElements})`"
          name="1"
          class="tabPaneTip"
          v-if="$hasAccess('task.query')"
        >
          <div class="li-container">
            <div class="li-box" v-for="(item, index) in tipList" @click="gotoBack(item)">
              <div class="li-top">
                <p :class="{'active':item.isRead==0}"
                   :style="{'text-decoration':item.businessType==3?'underline':'none'}">{{item.contentStr}}</p>
              </div>
              <div class="li-bottom">
                <div class="li-left">{{item.noticeTimeStr}}</div>
                <div class="li-right" v-show="item.isRead==0">
                  <a @click="setReaded(item,index)">{{item.action}}</a>
                </div>
              </div>
            </div>
          </div>
          <div class="bottom">
            <div class="bottom-left" @click="setMessage">{{$t_p('all_has_read')}}</div>
            <div class="bottom-right" @click="moreClick">{{$t_p('more')}}</div>
          </div>
        </TabPane>
        <TabPane :label="`${$t_p('upcoming')}(${taskList.length})`" name="2">
          <div class="li-container-task">
            <div class="li-box" v-for="(item, index) in taskList">
              <div class="li-top">
                <p :class="{'active':ind}">{{item.worderName}}</p>
              </div>
              <div class="li-bottom">
                <div class="li-left">{{item.createTime}}</div>
                <div class="li-right">
                  <a
                    @click="changeBgc(item,index)"
                    v-if="($hasAccess('border.query') && item.taskType == 1 )|| ($hasAccess('forder.query') && item.taskType == 2)"
                  >{{item.action}}</a>
                </div>
              </div>
            </div>
          </div>
        </TabPane>
      </Tabs>
    </div>
  </Poptip>
</template>

<script>
  import {getMessageList, setMessageReaded} from '../../api/message'
  import socket from '@/libs/socket'
  import {getTaskList} from '../../api/task'
  import {getCmdList} from '../../api/api'

  const iconMessage = require("../../../static/img/icon-message.png");

  // const getMessageStr = content => {
  //     if (content.author) {
  //       return (
  //         [
  //           content.deviceGroupName +
  //           i18nOptions.t('component.message-tip.tip_message')
  //         ].join("") || "无标题"
  //       );
  //     }
  //     else {
  //       return (
  //         [
  //           content.device_name ? "[" + content.device_name + "] " : "",
  //           content.worder_type ? "[" + content.worder_type + "] " : "",
  //           content.worder_name || "",
  //           content.businessStartTime
  //             ? "，将于" + content.businessStartTime + "开始"
  //             : ""
  //         ].join("") || "无标题"
  //       );
  //     }
  //   }
  // ;

  let tipIds = [];

  export default {
    name: "message-tip",
    i18n_prefix:'component.message-tip',

    data() {
      return {
        iconMessage,
        activeIndex: [],
        count: 5,
        ind: true,
        tipList: [],
        tipPageInfo: {
          pageIndex: 1,
          pageSize: 10,
          totalElements: 0
        },

        taskList: [],
        taskPageInfo: {
          pageIndex: 1,
          pageSize: 10,
          totalElements: 0
        }
      };
    },
    methods: {
      setMessage() {
        let notice = this.tipList.map(item => {
          return item.id;
        });
        setMessageReaded({
          noticeIds: notice
        }).then(res => {
          this.loadTipTableData();
        });
      },
      moreClick() {
        this.$router.push({
          name: "my-message"
        });
      },
      gotoBack(item) {
        if (item.businessType == 3) {
          this.$router.push({
            name: "device-summary-grouo-detail",
            params: {
              id: item.businessId
            }
          });
        } else {
          return;
        }
      },
      loadTipTableData(clear) {
        if (clear) {
          this.tipPageInfo.pageIndex = 1;
        }
        this.tipPageInfo.loading = true;
        getMessageList({
          pageNumber: this.tipPageInfo.pageIndex,
          pageSize: this.tipPageInfo.pageSize,
          isRead: 0
        })
          .then(res => {
            this.tipList = (res.data.result || []).map(d => {
              let content = {};
              try {
                content = JSON.parse(d.content);
              } catch (e) {
              }
              return {
                ...d,
                // contentStr: d.businessType == 5 ? d.content : getMessageStr(content),
                contentStr: d.content,
                action: d.businessType == 5 ? "" : this.$t_p('set_has_read')
              };
            });
            tipIds = this.tipList.map(d => d.id);
            this.tipPageInfo.totalElements = res.data.totalElements || 0;


            this.$emit("on-message-change", {
              tips: this.tipPageInfo.totalElements,
              tasks: this.taskPageInfo.totalElements,
              total:
                this.tipPageInfo.totalElements + this.taskPageInfo.totalElements
            });
          })
          .finally(() => {
            this.tipPageInfo.loading = false;
          });
      },
      loadTaskTableData(clear) {
        if (clear) {
          this.taskPageInfo.pageIndex = 1;
        }
        this.taskPageInfo.loading = true;
        getTaskList({
          // pageNumber: this.taskPageInfo.pageIndex,
          // pageSize: this.taskPageInfo.pageSize,
          handled: 2
          // taskType: taskType
        })
          .then(res => {
            this.taskList = (res.data.result || []).map(d => ({
              ...d,
              action: this.$t_p('handle')
            }));
            // this.taskPageInfo.totalElements = res.data.totalElements || 0;
            this.taskPageInfo.totalElements = this.taskList.length;

            this.$emit("on-message-change", {
              tips: this.tipPageInfo.totalElements,
              tasks: this.taskPageInfo.totalElements,
              total:
                this.tipPageInfo.totalElements + this.taskPageInfo.totalElements
            });
          })
          .finally(() => {
            this.taskPageInfo.loading = false;
          });
      },
      toMessageCenter() {
        this.$router.push({
          name: "my-task"
        });
      },
      setReaded(item, index) {
        this.tipList.splice(index, 1);
        this.$emit("on-message-change", {
          tips: this.tipPageInfo.totalElements - 1,
          tasks: this.taskPageInfo.totalElements,
          total:
            this.tipPageInfo.totalElements - 1 + this.taskPageInfo.totalElements
        });
        setMessageReaded({
          noticeIds: [item.id]
        }).then(res => {
          item.isRead = 1;
        });
      },
      changeBgc(item, index) {
        if (item.taskType == 2) {
          this.$router.push({
            name: "fault-work-detail",
            params: {id: item.worderId}
          });
        } else {
          this.$router.push({
            name: "maintenance-work-detail",
            params: {id: item.worderId}
          });
        }
      },
      getCmdList() {
        getCmdList({
        }).then(res => {
          let cmdList = res.data.result || [];
          this.$store.commit("setCmdList", cmdList);
        })
      },
      refresh() {
        this.loadTipTableData(true);
        this.loadTaskTableData(true);
      }
    },
    mounted() {
      // this.getCmdList();
      this.refresh();
      socket.$on("stationNotice", d => {
        if (JSON.parse(d).businessType != 4) {
          this.refresh();
        }
      });
      this.$eventHub.$on("global-message-refresh", () => {
        this.refresh();
      });
    }
  };
</script>

<style lang="less">
  @import "../../style/varient";
  .ivu-poptip-inner {
    /*width: 250px;*/
    height: 300px;
    background-color:@color-header-bg;
    border-radius: 0px 0px 5px 5px;
    overflow-y: auto;
  }

  .ivu-poptip-body {
    width: 100%;
    height: 100%;
    padding: 0;

    .ivu-poptip-body-content {
      width: 100%;
      height: 100%;

      .message-tip-content {
        width: 100%;
        height: 100%;

        .ivu-tabs-content {
          width: 100%;
          // height: 84% !important;
          // .ivu-tabs-tabpane {
          //   overflow-y: scroll;
          // }
        }
      }
    }
  }

  .ivu-tabs-nav-wrap {
    margin-bottom: 0;
  }

  .ivu-tabs-ink-bar {
    bottom: -1px;
  }

  .ivu-tabs-nav {
    border-bottom: 1px solid #303337;
  }

  .ivu-tabs-bar {
    margin-bottom: 10px;
  }

  .ivu-poptip-popper[x-placement^="bottom"] .ivu-poptip-arrow:after {
    border-bottom-color: @color-header-bg;
  }

  .ivu-poptip-popper[x-placement^="bottom"] .ivu-poptip-arrow {
    top: 0.054545rem;
    border-width: 0 0.127273rem 0.127273rem;
    border-bottom-color: rgba(0, 0, 0, 0.1);
  }

  .message-tip-tab {
    .ivu-tabs-nav {
      width: 100%;
    }

    .ivu-poptip-inner {
      background: #191d21;
    }

    .ivu-tabs-tab {
      width: 45%;
      text-align: center;
      /*margin: 0;*/
    }
  }
</style>
<style lang="less" scoped>
  @import "../../style/varient";
  .message-tip {
    display: inline-block;
    cursor: pointer;

    img {
      width: 18px;
      height: 18px;
      vertical-align: middle;
    }
  }

  .ivu-tabs {
    height: 100%;
  }

  .tabPaneTip {
    position: relative;
    padding-bottom: 41px;

    .li-container {
      position: absolute;
      top: 0;
      height: 206px;
      overflow-y: scroll;
      width: 100%;
    }

    .bottom {
      border-top: 1px solid #515a6e;
      position: absolute;
      top: 205px;
      display: flex;
      height: 43px;
      line-height: 43px;
      width: 100%;
      cursor: pointer;

      .bottom-left {
        width: calc(~"50% - " 1px);
        text-align: center;
        border-right: 1px solid #515a6e;
      }

      .bottom-right {
        width: 50%;
        text-align: center;
      }

      div:hover {
        color: @color-primary
      }
    }
  }

  .li-container-task {
    overflow-y: scroll;
    height: 250px;
  }

  /*滚动条样式*/
  ::-webkit-scrollbar {
    /*滚动条整体样式*/
    width: 4px; /*高宽分别对应横竖滚动条的尺寸*/
    height: 4px;
  }

  ::-webkit-scrollbar-thumb {
    /*滚动条里面小方块*/
    border-radius: 5px;
    // -webkit-box-shadow: inset 0 0 5px rgba(114, 104, 104, 0.8);
    background: @color-primary;
  }

  ::-webkit-scrollbar-track {
    /*滚动条里面轨道*/
    // -webkit-box-shadow: inset 0 0 5px rgba(114, 104, 104, 0.8);
    border-radius: 0;
    background: black;
  }

  .li-box {
    display: flex;
    flex-direction: column;
    padding-bottom: 10px;
    border-bottom: 1px solid #303337;
    margin: 0 15px 10px 15px;

    .li-top {
      p {
        width: 100%;
        font-size: 12px;
        line-height: 18px;
        color: #ffffff;
        height: auto;
        word-wrap: break-word;
        word-break: break-all;
        white-space: pre-wrap;
      }

      .active {
        color: @color-primary;
      }
    }

    .li-bottom {
      display: flex;

      .li-left {
        font-size: 12px;
        line-height: 20px;
        color: #969696;
        text-align: left;
      }

      .li-right {
        flex: 1;
        text-align: right;

        a {
          font-size: 12px;
          line-height: 16px;
          color: @color-primary;
        }
      }
    }
  }
</style>



