import axios from '@/libs/api.request'

export const getTaskList = ({
                                    pageNumber,
                                    pageSize,
                                 handled,
                                 taskType
                                  }) => {
  return axios.request({
    url: '/order/listTask',
    params: {
      pageNumber,
      pageSize,
      handled,
      taskType
    },
    method: 'get'
  })
};



